import React from 'react';
import { useQuery } from 'react-query';
import { Typography, makeStyles } from '@material-ui/core';
import ProductCard from './productCard';
import getProducts from '../services/products/getProducts';
import Loading from './loading';
import getAd from '../services/products/getAd';
import AdvertisementCard from './advertisementCard';
import { ArrowBack, ArrowForward } from '@material-ui/icons';
import Slider from 'react-slick';
import { Col, Row } from 'antd';

const useStyles = makeStyles((theme) => ({
  newArrivals: {
    padding: '2rem 2rem 1rem 2rem',
    [theme.breakpoints.down('xs')]: {
      padding: '10px',
    },
  },
  newArrivals_items: {
    display: 'flex',
    flexWrap: 'wrap',
    justifyContent: 'center',
    '& > *': {
      margin: '10px',
    },
  },
  sliderContainer: {
    width: '100%',
    padding: '0px 20px',
    // [theme.breakpoints.down('sm')]: {
    //   marginLeft: '1rem',
    // },
  },
}));

const NewArrivals = () => {
  const classes = useStyles();

  const { isLoading, isError, data, error } = useQuery('products', getProducts);

  const {
    isLoading: isAdLoading,
    isError: isAdError,
    data: adData,
  } = useQuery('ad', getAd);

  console.log('newArrivals', data);

  const showAdvertisementCard = () => {
    if (isAdLoading) {
      return <Loading />;
    }
    if (isAdError) {
      return;
    }
    return adData.map((item, i) => (
      <Col xs={24} md={24} lg={12}>
        <Row style={{ width: '100%' }} justify="center">
          <AdvertisementCard
            key={i}
            title={item.ad_title}
            subtitle={item.ad_desc}
            id={item.id}
            image={item.image}
            products={item.products}
          />
        </Row>
      </Col>
    ));
  };

  if (isLoading) {
    return <Loading />;
  }

  if (isError) {
    return <h1>{error}</h1>;
  }

  const SampleNextArrow = (props) => {
    const { className, onClick } = props;
    return (
      <div className={className} onClick={onClick}>
        <ArrowForward
          size={30}
          style={{
            color: '#000',
            background: '#FFFFFF',
            border: '1px solid #000',
            borderRadius: '50%',
            padding: '4px',
          }}
        />
      </div>
    );
  };

  const SamplePrevArrow = (props) => {
    const { className, onClick } = props;
    return (
      <div className={className} onClick={onClick}>
        <ArrowBack
          size={30}
          style={{
            color: '#000',
            background: '#FFFFFF',
            border: '1px solid #000',
            borderRadius: '50%',
            padding: '4px',
          }}
        />
      </div>
    );
  };

  const settings = {
    dots: false,
    infinite: true,
    autoplay: true,
    centerMode: false,
    speed: 1000,
    autoplaySpeed: 3000,
    slidesToShow: 3,
    slidesToScroll: 2,
    nextArrow: <SampleNextArrow />,
    prevArrow: <SamplePrevArrow />,
    responsive: [
      {
        breakpoint: 1080,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 1,
        },
      },
      {
        breakpoint: 700,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
        },
      },
    ],
  };

  return (
    <div className={classes.newArrivals}>
      <Typography
        color="textPrimary"
        variant="h5"
        gutterBottom
        align="center"
        style={{ fontWeight: 'bold' }}
      >
        NEW ARRIVALS
      </Typography>
      <div className={classes.sliderContainer} align="middle">
        <Slider {...settings}>
          {data &&
            data.map((product) => {
              return (
                <ProductCard
                  key={product.id}
                  name={product.product_name}
                  subtitle={product.description}
                  image={`${process.env.REACT_APP_UPLOAD_URL}${product.images[0].image}`}
                  price={product.price}
                  discount={product.discount_amount}
                  id={product.id}
                  bestSeller={product.best_seller}
                  brandName={product.brand && product.brand.brand_name}
                />
              );
            })}
        </Slider>
      </div>
      <Row style={{ width: '100%' }} justify="center">
        <Row
          style={{ width: '100%' }}
          justify="center"
          align="middle"
          gutter={[16, 0]}
        >
          {showAdvertisementCard()}
        </Row>
      </Row>
    </div>
  );
};

export default NewArrivals;
