import React from 'react';

import {
  Card,
  Typography,
  Button,
  makeStyles,
  CardActions,
  CardContent,
  Chip,
} from '@material-ui/core';
import { useHistory } from 'react-router-dom';

const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
    minHeight: 350,
    marginTop: '4rem',
    marginBottom: '2.5rem',
    backgroundPosition: 'center',
    backgroundSize: 'cover',
    color: 'white',
    position: 'relative',
  },
}));

const AdvertisementCard = ({ title, subtitle, image, id, products }) => {
  const classes = useStyles();
  const history = useHistory();

  return (
    <Card
      className={classes.root}
      style={{
        backgroundImage: `url(${process.env.REACT_APP_UPLOAD_URL}${image})`,
      }}
    >
      <CardContent>
        <Chip
          size="medium"
          label={title}
          color="primary"
          style={{ fontSize: '1.2rem', color: 'white', fontWeight: 'bold' }}
        />
        <Typography variant="subtitle1" gutterBottom>
          {subtitle}
        </Typography>
      </CardContent>
      <CardActions>
        <Button
          color="primary"
          variant="contained"
          style={{
            width: '150px',
            position: 'absolute',
            bottom: '1rem',
            left: '1rem',
            color: '#fff',
          }}
          onClick={() =>
            history.push(`/ad_products/${id}`, { products: products })
          }
          size="large"
        >
          Shop Now
        </Button>
      </CardActions>
    </Card>
  );
};

export default AdvertisementCard;
